import Enum from './enumType';
let enums = {};

enums.AccountPostingType = () => {
    return new Enum([
        {key: 'balanceSheet', display: '  '},
        {key: 'benefitAndLoss', display: '  '},
        {key: 'entezami', display: ''}
    ]);
};

enums.AccountBalanceType = () => {
    return new Enum([
        {key: 'debit', display: ''},
        {key: 'credit', display: ''}
    ]);
}

enums.AssignmentStatus = () => {
    return new Enum([
        {key: 'Required', display: ' '},
        {key: 'NotRequired', display: ' '},
        {key: 'DoesNotHave', display: ''}
    ]);
}

enums.JournalType = () => {
    return new Enum([
        {key: 'Opening', display: ''},
        {key: 'Closing', display: ''},
        {key: 'FixedAsset', display: ''},
        {key: 'Payroll', display: ''},
        {key: 'Special', display: ''}
    ]);
}

enums.JournalStatus = () => {
    return new Enum([
        {key: 'Temporary', display: ''},
        {key: 'BookKeeped', display: ' '},
        {key: 'Fixed', display: ' '}
    ]);
};

enums.Active = () => {
    return new Enum([
        {key: true, name: 'showActiveItems', display: '  '},
        {key: false, name: 'showInactiveItems', display: '   '}
    ]);
};

enums.ChequeCategoryStatus = ()=> {
    return new Enum([
        {key: 'Open', display: ''},
        {key: 'Closed', display: ''}
    ]);
};

enums.AccMode = () => {
    return new Enum([
        {key: 'create', display: ' '},
        {key: 'audit', display: ' '}
    ]);
};

enums.getMonth = function () {
    return new Enum([
        {key: 1, display: ""},
        {key: 2, display: ""},
        {key: 3, display: ""},
        {key: 4, display: ""},
        {key: 5, display: ""},
        {key: 6, display: ""},
        {key: 7, display: ""},
        {key: 8, display: ""},
        {key: 9, display: ""},
        {key: 10, display: ""},
        {key: 11, display: ""},
        {key: 12, display: ""}
    ]);
};

export default enums;

